import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './About.css';
class About extends Component {
  constructor(props) {
    super(props);
  }

  render = () => {
    return (<div className="route-wrapper--about">
<div className="route-wrapper--about__wrapper">
<div className="route-wrapper--about__wrapper__container">
<h1>About me</h1>
<h2><i className="fas fa-birthday-cake route-wrapper--about__wrapper__container__i"></i>
I'm <span className="route-wrapper--about__wrapper__container__important">14</span> years old</h2>
<h2><i className="fas fa-map-marker-alt route-wrapper--about__wrapper__container__i"></i>
Warsaw, <span className="route-wrapper--about__wrapper__container__important">Poland</span> based</h2>
<h2><i className="fas fa-graduation-cap route-wrapper--about__wrapper__container__i"></i>
Assistant at <span className="route-wrapper--about__wrapper__container__important">Websites Designing Lab</span> in Palace of Youth in WAW</h2>
<h2><i className="fas fa-lightbulb route-wrapper--about__wrapper__container__i"></i>
I have an <span className="route-wrapper--about__wrapper__container__important">4</span> years of experience with
</h2>
<div className="route-wrapper--about__wrapper__container_apps-container">
<div className="route-wrapper--about__wrapper__container_apps-container__app">
<i class="fas fa-globe"></i>
<h3>Web applications</h3>
</div>
<div className="route-wrapper--about__wrapper__container_apps-container__app">
<i class="fas fa-mobile"></i>
<h3>Mobile apps</h3>
</div>
<div className="route-wrapper--about__wrapper__container_apps-container__app">
<i class="fas fa-desktop"></i>
<h3>Desktop apps</h3>
</div>
<div className="route-wrapper--about__wrapper__container_apps-container__app">
<i class="fas fa-paint-brush"></i>
<h3>Graphics</h3>
</div>
</div>
<div className="route-wrapper--about__wrapper__container__buttons">
<Link to="/moreaboutme">
<span className="route-wrapper--about__wrapper__container__buttons__btn"><i class="fas fa-info-circle route-wrapper--about__wrapper__container__buttons__btn__i"></i>Read more</span>
</Link>
<Link to="/portfolio">
<span className="route-wrapper--about__wrapper__container__buttons__btn"><i class="fas fa-code route-wrapper--about__wrapper__container__buttons__btn__i"></i>Portfolio</span>
</Link>
</div>
</div>
</div>
    </div>);
  }

}

export default About;



