import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './BrowserToolbar.css';
class BrowserToolbar extends Component {
  constructor(props){
    super(props);
  }

  render = () => {
    console.log(this.props.children);
    return (
      <div className="browser">
    <div className="browser__toolbar">
    <div className="browser__toolbar__inner">
      <div className="browser__toolbar__col--left">
        <span className="browser__toolbar__button" style={{background: '#ED594A'}}></span>
        <span className="browser__toolbar__button" style={{background:'#FDD800'}}></span>
        <span className="browser__toolbar__button" style={{background:'#5AC05A'}}></span>
      </div>
      <div className="browser__toolbar__col--middle">
        <div className="browser__toolbar__input">{this.props.children}</div>
      </div>
      <div className="browser__toolbar__col--right">
      <i className="fas fa-bars"></i>
      </div>
    </div>
  </div>
  </div>
    );
  }

}

export default BrowserToolbar;



