import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Typist from 'react-typist';
import './MoreAboutMe.css';
const Line = (props) => {
  return (
    <span className="route-wrapper--more-about-me__wrapper__line">
    <span className="route-wrapper--more-about-me__wrapper__line--indicator">
    <span className="route-wrapper--more-about-me__wrapper__line--indicator--user">micorix@micorix-me</span>
    <span className="route-wrapper--more-about-me__wrapper__line--indicator--colon-dollar">:</span>
    <span className="route-wrapper--more-about-me__wrapper__line--indicator--tilde">~</span>
    <span className="route-wrapper--more-about-me__wrapper__line--indicator--colon-dollar">$</span>
    </span>
    <span className="route-wrapper--more-about-me__wrapper__line--indicator--command">
    <Typist cursor={{
        hideWhenDone: true,
        hideWhenDoneDelay: 100
      }} startDelay={2000} onTypingDone={props.onTyped}>
      {props.children}</Typist>
    </span>
    </span>
  );
}
class MoreAboutMe extends Component {
  constructor(props) {
    super(props);
  }
onTyped = () =>{
alert('done');
}
  render = () => {
    return (<div className="route-wrapper--more-about-me">
<div className="route-wrapper--more-about-me__wrapper">
<div className="route-wrapper--more-about-me__wrapper__line--first">
<Line onTyped={this.onTyped}>man micorix</Line>
</div>
<div className="route-wrapper--more-about-me__wrapper__help">
:q - Quit
</div>
</div>
    </div>);
  }

}

export default MoreAboutMe;



