import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Home.css';
import Typist from 'react-typist';
import BrowserToolbar from './BrowserToolbar/BrowserToolbar';
import Message from './Message/Message';
class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      passionVisible: false,
      stripesVisible: false,
      shadow: {
        top: 10,
        left: 1,
        opacity: 0
      },
      nextVisible:false
    };

    document.addEventListener('mousemove', e => {
      this.setState({nextVisible:true})
      let shadowLimit = 5;
      let center = {
        x: window.innerWidth / 2,
        y: window.innerHeight / 2
      };
      let shadowX = (center.x - e.clientX) / 10;
      let shadowY = (center.y - e.clientY) / 10;

      shadowX = (shadowX > shadowLimit)
        ? shadowLimit
        : shadowX;
      shadowX = (shadowX < shadowLimit * -1)
        ? shadowLimit * -1
        : shadowX;
      shadowY = (shadowY > shadowLimit)
        ? shadowLimit
        : shadowY;
      shadowY = (shadowY < shadowLimit * -1)
        ? shadowLimit * -1
        : shadowY;
        let shadow = Object.assign({}, this.state.shadow);
        shadow.top = shadowY;
        shadow.left = shadowX;
      this.setState({shadow})
    })
  }
onTyped = () => {
this.setState({passionVisible:true})
}
onAnimationEnd = () => {
  let shadow = Object.assign({}, this.state.shadow);
  shadow.opacity = 1;
this.setState({shadow, nextVisible:true})
}
  render = () => {
    return (<div className="route-wrapper--home">
  <Link to="/about">
<i className={`route-wrapper--home__arrow fas fa-chevron-circle-right animated ${this.state.nextVisible ? 'fadeIn' : ''}`}></i>
</Link>
      <div className="route-wrapper--home__container">
        <div className="route-wrapper--home__container__header-container">
          <div className="route-wrapper--home__header-wrapper">
            <h1 className="route-wrapper--home__header-wrapper__header">
              <Typist cursor={{
                  hideWhenDone: true,
                  hideWhenDoneDelay: 100
                }} onTypingDone={this.onTyped}>
                Because app is the code
                <Typist.Backspace count={8} delay={500}/>
                not the code
              </Typist>
            </h1>
            <h1 className="route-wrapper--home__header-wrapper__header--shadow" style={this.state.shadow}>Because app is not the code</h1>
          </div>
        </div>
        <div className="route-wrapper--home__container__header-container">
          <div className="route-wrapper--home__header-wrapper">
            <h1 className={`route-wrapper--home__header-wrapper__header animated
              ${this.state.passionVisible ? 'bounceInLeft' : ''}`}
              onAnimationEnd={this.onAnimationEnd}>It's a passion</h1>
            <h1 className="route-wrapper--home__header-wrapper__header--shadow" style={this.state.shadow}>It's a passion</h1>
          </div>
        </div>
      </div>
    </div>);
  }

}

export default Home;



