import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './Message.css';
class Message extends Component {
  constructor(props){
    super(props);
  }

  render = () => {
    console.log(this.props.children);
    return (
      <div className="message">
      <span className="message__text">
            {this.props.writing == true ? <span className="message__text__dots"></span> : this.props.children}
      </span>
      {
        (() => {
          if(!this.props.writing){
            return (<span className="message__date">
            {`${new Date().getHours()}:${new Date().getMinutes().toString().length == 1 ? '0'+new Date().getMinutes() : new Date().getMinutes()}`}
            </span>);
          }
        })()
      }

      </div>
    );
  }

}

export default Message;



